// back-to-top styles
.back-to-top {
  width: 35px;
  height: 35px;
  padding: 0;

  cursor: pointer;

  color: $black;
  border: none;
  border-radius: 5px;
  border-radius: 50%;
  outline: none;
  background: $gold-light;

  @include transition('background', .3s);

  .icon {
    font-size: 1em;
    line-height: 35px;

    display: block;

    text-align: center;
  }
}


@include mobile-tablet {
  .back-to-top {
    position: sticky;
    right: 15px;
    bottom: 15px;

    transform: translateY(0);

    @include transition('transform, visibility, opacity', .25s, ease-in-out);

    &.is-hidden,
    &.notify-open {
      visibility: hidden;

      transform: translateY(10px);

      opacity: 0;
    }


    &.no-fixed {
      position: absolute;
      top: -50px;
    }
  }
}

@include desktop {
  .back-to-top {
    position: absolute;
    top: -50px;
    right: 20px;

    &:hover {
      background: #aaa;
    }
  }
}


