// default-btn styles
.default-btn {
  position: relative;

  display: inline-block;

  min-width: 150px;
  margin: 15px 15px 15px 0;
  padding: 3px 30px;

  text-align: center;
  vertical-align: middle;
  text-transform: uppercase;

  color: $white;
  border: 1px solid $green;
  border-radius: 20px;
  background: $green;
  
  @include transition('background, border', 200ms);

  &:hover {
    color: $green;
    border: 1px solid $green;
    background: $white;
  }

  .icon {
    display: inline-block;

    margin-left: 5px;

    @include transition('transform', 200ms);
  }

  .visible {
    display: inline-block;
  }

  .hidden {
    display: none;
  }

  &.open {
    .icon {
      transform: rotate(-180deg);
    }

    .visible {
      display: none;
    }

    .hidden {
      display: inline-block;
    }
  }

  &.no-margin {
    margin: 0;
  }
}
