// notification styles
.notifications {
  @include gradient();

  .notification {
    font-size: 1em;

    position: relative;

    padding: 10px 0;

    &__link {
      display: block;

      width: 100%;

      text-decoration: none;
    }

    .notification-arrow {
      font-size: 1.4em;
      line-height: 20px;

      position: absolute;
      z-index: 2;

      width: 20px;
      height: 20px;
      padding: 0;

      text-align: center;

      color: $white;
      border: none;
      background: none;
      // @include vertical-align;
    }

    .custom_paging {
      font-size: calc-em(14px);

      position: absolute;
      right: 70px;

      width: 30px;

      text-align: center;
      top: 50%;
      transform: translateY(-50%);

      @extend %plainlist;

      li {
        display: none;
        &.slick-active {
          display: block;
        }
      }
    }
  }
}



@include mobile-tablet {
  .notification {
    z-index: 4;
    bottom: 0;

    width: 100%;
    padding-right: 40px;
    padding-bottom: 40px;

    transform: translateY(0);

    @include transition('transform, visibility, opacity', 200ms, ease-in-out);;

    &.mbl-hidden {
      visibility: hidden;

      transform: translateY(100%);

      opacity: 0;
    }

    .notification-arrow,
    .custom_paging {
      bottom: -25px;
      color: $white;
    }
    .notification__carousel {
      margin-bottom: 0;

      .notification-next {
        left: 50px;
      }
      .notification-prev {
        left: 0;
      }
      .custom_paging {
        left: 20px;
      }
    }

    &__link {
      color: $white;
    }
  }

  .notification-btn {
    position: sticky;
    z-index: 4;
    right: 15px;
    bottom: 65px;

    width: 35px;
    height: 35px;
    padding: 0;

    cursor: pointer;

    color: $black;
    border: none;
    border-radius: 50%;
    outline: none;
    background: $gold-light;

    @include transition('background', .3s);
    @include transition('transform, visibility, opacity', 200ms, ease-in-out);

    .icon {
      font-size: 1.3em;
      line-height: 35px;

      display: block;

      text-align: center;
    }

    .icon-alarm {
      transform: translateY(0);

      @include transition('transform, visibility, opacity', 200ms, ease-in-out);
    }
    .icon-close {
      visibility: hidden;

      transform: translateY(10px);

      opacity: 0;

      @include transition('transform, visibility, opacity', 200ms, ease-in-out);
    }

    &.active {
      .icon-alarm {
        visibility: hidden;

        transform: translateY(10px);

        opacity: 0;
      }
      .icon-close {
        visibility: visible;

        transform: translateY(-100%);

        opacity: 1;
      }
    }

    // if no back to top button, push the button down to bottom
    &.no-back-btn {
      transform: translateY(50px);
    }
  }
}

@include desktop {
  .notification-section {
    visibility: hidden;
    overflow: hidden;

    width: 100%;
    // height: 0;
    max-height: 0;

    opacity: 0;

    @include transition('visibility, opacity, max-height, overflow', 350ms, 'linear');

    &.is-visible,
    &.active {
      visibility: visible;

      height: auto;
      max-height: 70px;

      opacity: 1;
    }
  }

  .notification {
    .notification-btn {
      position: absolute;
      top: 3px;
      right: 15px;

      padding: 0;

      border: none;
      outline: none;
      background: none;
    }

    .icon-close {
      font-size: 1.4em;
    }

    .is_stuck & {
      background: rgba(255, 255, 255, 1);
    }

    .notification-arrow,
    .custom_paging {
      top: 0;
    }

    .notification__carousel {
      padding-right: 150px;
      margin-bottom: 0;

      .notification-next {
        right: 50px;
      }
      .notification-prev {
        right: 100px;
      }
    }
  }
}

@media (min-width: 1024px) and (max-width: 1320px) {
  .notification {
    .notification-btn {
      right: 70px;
    }

    .notification__carousel {
      padding-right: 200px;

      .notification-next {
        right: 100px;
      }
      .notification-prev {
        right: 150px;
      }

      .custom_paging {
        right: 120px;
      }
    }
  }
}

@include mobile-tablet {
  .notifications {
    display: none;
  }
}
