// accordion styles
.accordion {
  overflow: hidden;
}

.accordion__item {
  position: relative;

  margin-bottom: 10px;


  &.is-active {
     > .accordion__title {
      &:after {
        color: $body-text;

        @include rotate(-180deg);
      }
    }

    .accordion__content {
      @extend .clearfix;
    }
  }
}


.accordion__title {
  font-weight: $semi-bold;

  position: relative;

  display: block;

  padding: 10px 30px;

  cursor: pointer;

  color: $body-text;
  background: $grey;

  &:hover {
    text-decoration: none;

    color: $green;
  }

  &:focus {
    text-decoration: none;

    color: $body-text;
  }

  &:after {
    font-size: 1.3em;

    position: absolute;
    right: 20px;

    margin: auto;

    content: '\e907';

    color: $green;

    @extend %icomoon;
    @include transition(transform, .35s, ease-in-out);
  }
}

.accordion__content {
  display: none;

  margin-bottom: 15px;
  padding: 10px 30px;

  @include mobile {
    padding: 10px 10px;
  }

  table {
    width: 100%;

    tr {
      border-bottom: 1px solid $silver;

      &:last-of-type {
        border-bottom: none;
      }
    }

    td {
      padding: 5px 0;

      &:nth-child(3) {
        display: none;
      }
    }
  }
}

