// whats-coming styles
.whats-coming {

  .slick-dots {
    bottom: 15px;
    left: 0;

    li {
      button {
        height: 10px;
        width: 10px;
        border-radius: 10px;
        border: 1px solid $white;
        background-color: transparent;


        &:before {
          content: none;
        }
      }

      &.slick-active {
        button {
          background-color: $white;
        }
      }
    }
  }

  .container {
    padding: 50px 20px 70px;
  }

  .slick-dotted.slick-slider {
    margin-bottom: 0;
  }

  .slick-arrow {
    height: 50px;
    width: 50px;
    left: 118.667px;
    z-index: 3;
    top: auto;

    &:before {
      font-size: 50px;
      opacity: 1;
      cursor: pointer;
    }
  }

  .slick-prev {
    bottom: 20%;

  }

  .slick-next {
    bottom: 0;
  }

  &__imagewrap {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    width: 70%;
    height: 100%;
    max-height: 370px;
    margin-left: auto;
    margin-right: 40px;
    @include box-shadow(8px, 7px, 15px, 5px, rgba(0, 0, 0, .4));
  }

  &__header {
    letter-spacing: .2em;
    display: inline-block;
    position: absolute;
    z-index: 1;
    top: 95px;
    display: flex;

    .line {
      @include line(100px);
      position: relative;
      top: 18px;
    }

    .title {
      display: inline-block;
      font-size: calc-em(70px);
      letter-spacing: .2em;
      line-height: 1;
      margin: 0;
      padding-left: 20px;

      span {
        display: block;
      }
    }
  }

  &__each {
    display: flex !important; // to override slick's display inline-block
  }

  &__title {
    @include font-family('demi');
    font-size: calc-em(28px);
  }

  &__copy {
    @include font-family('light');
    font-size: calc-em(18px);
    margin-top: 0;
  }

  &__image {
    padding-right: 1em;
  }
}

@include mobile-tablet {
  .whats-coming {

    .slick-dots {
      top: 330px;
      bottom: auto;
    }

    &__title {
      font-size: calc-em(24px);
    }

    &__copy {
      font-size: calc-em(18px);
    }

    &__each {
      .col-3-md {
        padding-top: 30px;
        padding-left: 25px;
        padding-right: 25px;
      }
    }

    &__header {
      .title {
        font-size: calc-em(50px);
      }

      .line {
        @include line(50px);
      }
    }

    &__imagewrap {
      height: 320px;
      margin-right: auto;
      width: calc(100% - 30px);
      box-shadow: none;
    }

    .slick-prev {
      top: 34%;
    }

    .slick-next {
      top: 48%;
    }

    .slick-arrow {
      left: auto;
      right: 25px;
    }
  }
}

@include mobile {
  .whats-coming {

    &__each {
      .col-3-md {
        padding-top: 25px;
      }
    }

    .slick-dots {
      top: 275px;
    }

    .container {
      padding-bottom: 40px;
    }

    &__header {
      top: 5px;
    }

    &__imagewrap {
      height: 270px;
      width: 100%;
    }

    .slick-prev {
      top: 23%;
    }

    .slick-next {
      top: 32%;
    }
  }
}
