// site-footer styles
.footer {
  font-size: calc-em(16px);

  position: relative;
  z-index: 15;

  padding: 30px 0 20px;

  color: $white;
  background: $footer-color;

  p,span,a {
    @include font-family('book');
  }

  .container {
    width: auto;
  }

  &__logos {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }

  &__top {
    justify-content: center;
    white-space: nowrap;

    p {
      font-size: calc-em(14px);
      text-decoration: uppercase;
    }

    img {
      margin-right: 35px;
      margin-bottom: 15px;
    }

    .col-8-md,
    .col-10-md {
      display: flex;
      flex-direction: row;
      justify-content: center;
    }

    .col-4-md {
      display: flex;
      flex-direction: column;
    }
  }

  &__sponsor {
    border-right: 1px solid $darkgrey;
    margin: 2em 0 3em;
    padding: 0 50px;


    &:last-of-type {
      border-right: none;
    }

    p {
      margin-top: 0;
      margin-bottom: 15px;
    }

    img {
      height: 36px;
    }

    &.col-5-md {
      padding: 0 3em;

      &:first-of-type {
        padding-right: 2em;
      }
    }
  }

  &__right {
    padding-bottom: 20px;

    text-align: right;
  }

  &__bottom {
    padding: 1.5em 15px 0;
    border-top: 1px solid $darkgrey;
  }

  .subscribe {
    display: flex;
    justify-content: flex-end;
    align-items: center;

    &__text {
      font-size: calc-em(16px);
      color: $pale-grey;
      padding-right: 1em;
    }

    &__btn {
      border: 1px solid $yellow;
      background-color: transparent;
      font-size: calc-em(13px);
      @include font-family('demi');

      border-radius: 18px;
      padding: 5px 18px;
    }
  }

  .connect {
    display: flex;
    justify-content: flex-end;
    align-items: center;

    margin-top: 1em;

    &__text {
      padding-right: 1em;
    }

    ul {
      li {
        display: inline-block;
        padding: 0 .5em;
        a {
          text-decoration: none;
          color: $white;
        }
      }
    }
  }

  .last-updated {
    margin-top: 0;
    color: $pale-grey;
  }
}

@include mobile-tablet {
  .footer {
    padding-bottom: 50px;
    text-align: center;

    .subscribe,
    .connect {
      justify-content: center;
    }

    &__bottom {
      flex-direction: column-reverse;
    }

    &__sponsor {
      border-right: none;
    }

    &__right {
      text-align: center;
    }

    &__top {

      p {
        font-size: calc-em(14.05px);
        @include font-family('demi');
        text-align: left;

        padding-left: 15px;
      }

      img {
        margin: .5em 15px;
      }

      .col-4-md,
      .col-2-md,
      .col-5-md {
        display: block;
      }

      .col-4-md {
        &:last-of-type {
          img {
            &:first-of-type {
              margin-right: 1em;
            }
            &:last-of-type {
              margin-left: 1em;
            }
          }
        }
      }

      .col-5-md {
        &:first-of-type {
          img {
            margin-left: .5em;
            margin-right: .5em;
          }
        }
      }
    }
  }
}

@include mobile {
  .footer {

    &__top {
      .col-8-md,
      .col-10-md {
        flex-direction: column;
      }

      .col-4-md {
        margin: 1.5em 0;
      }
    }

    .connect {
      justify-content: center;
    }

    .subscribe {
      flex-direction: column;

      &__btn {
        margin-top: 10px;
      }
    }

    &__sponsor {
      padding-left: 0;

      img {
        height: 30px;
      }
    }
  }
}
