// installation-item styles

.installations-listing {

  &__header {
    @include font-family('demi');
    font-size: calc-em(60px);
    letter-spacing: .2em;
    position: absolute;
    top: 100px;
    z-index: 2;
    display: flex;
    flex-direction: row;

    .line {
      @include line(100px);
      position: relative;
      top: 33px;
    }

    h1 {
      margin: 0;
      padding-left: 20px;
      letter-spacing: .2em;

      span {
        line-height: 1;
        display: block;
        position: relative;

        &:nth-of-type(2) {
          left: 250px;
        }

        &:nth-of-type(3) {
          left: 135px;
        }
      }
    }
  }

  .container {
    padding: 0 60px 70px;
    white-space: nowrap;
  }

  &__each {
    padding: 30px;

    &:hover {
      .installations-listing__overlay {
        opacity: 1;
        transition: opacity .3s;
      }
    }

    &:first-of-type {
      padding-top: 230px;
    }

    &.wide {
      width: 45%;
    }

    &.narrow {
      width: 41%;
    }
  }

  &__overlay {
    height: 100%;
    width: 100%;
    position: absolute;
    left: 0;
    top: 0;
    background-color: rgba(0,0,0,.7);
    opacity: 0;
    transition: opacity .3s;
    display: flex;
    align-items: center;
    justify-content: center;

    p {
      @include font-family('demi');
      font-size: calc-em(30.58px);
      color: $white;
      letter-spacing: .2em;
    }
  }

  &__image {
    overflow: hidden;
    background-size: 125%; // TODO: get images without spacing around
    background-position: center;
    @include box-shadow(8px, 7px, 15px, 5px, rgba(0, 0, 0, .4));
    margin-bottom: 1em;
    position: relative;

    img {
      visibility: hidden;
      opacity: 0;
    }
  }

  &__content {
    .title {
      font-size: calc-em(28px);
      @include font-family('demi');
      margin: 0;
    }

    .artists {
      font-size: calc-em(18px);
      @include font-family('light');
      margin-top: 0;
      white-space: normal;
    }
  }
}

@include mobile {
  .installations-page {
    padding-left: 15px;

  }
  .installations-listing {

    .container {
      padding: 0 30px 52.22px;
    }

    &__header {
      font-size: calc-em(50px);
      left: -30px;
      top: 25px;
      position: relative;

      .line {
        @include line(50px);
        top: 24px;
      }

      h1 {
        letter-spacing: .17em;

        span {
          &:nth-of-type(2) {
            left: 120px;
          }

          &:nth-of-type(3) {
            left: 0;
          }
        }
      }
    }

    &__content {
      .artists {
        font-size: calc-em(20px);
        padding-right: 30px;
      }
    }

    &__each {
      padding: 0;
      margin-bottom: 35px;
      display: block;

      &:hover {
        .installations-listing__overlay {
          opacity: 0;
        }
      }

      &:first-of-type {
        padding-top: 0px;
      }

      &.wide,
      &.narrow {
        width: 100%;
      }
    }
  }
}
