// font-resizer styles
.font-resizer {
	position: relative;
	z-index: 2;
	text-align: center;
	display: inline-block;
	width: 120px;
	padding: 15px 0;
	margin-right: 20px;

	button {
		padding: 0 3px;

		color: $darkgrey;
		border: 0;
		outline: 0;
		background: none;

		&[data-size='normal'] {
			font-size: calc-em(15px);
		}

		&[data-size='large'] {
			font-size: calc-em(18px);
		}

		&[data-size='larger'] {
			font-size: calc-em(21px);
		}

		&.is-active,
		&:hover,
		&:focus {
			color: $white;
		}
	}
}

@include mobile-tablet {
	.font-resizer {
		display: none;
	}
}
