// primary-menu styles

.main-menu {
	position: relative;
	z-index: 10;

	width: 100%;

	.container {
		width: auto;
		padding-left: 20px;
		padding-right: 20px;
	}

	a {
		position: relative;

		text-decoration: none;
		cursor: pointer;
		@include font-family('book');

		&:hover {
			color: $white-light;
			text-decoration: none;
		}
	}

	&__lvl1,
	.main-menu__lvl2,
	.main-menu__lvl3,
	.main-menu__lvl4 {
		width: 100%;

		@extend %plainlist;
		//
	}

	.main-menu__lvl2,
	.main-menu__lvl3 {
		// display: none;
		visibility: hidden;

		opacity: 0;
	}

	.notify-menu {
		.js-notify-btn {
			text-align: center;

			color: $white;
			border: none;
			background: none;

			.icon {
				font-size: 1em;
				display: block;
				margin-top: 3px;
			}

			&.active {
				color: #d8cba3;
			}
		}
	}

	&__lvl1-item-link {
		font-size: calc-em(18px);
		padding: 15px;
	}
}

@include mobile-tablet {
	.main-menu {
		position: fixed;
		z-index: 12;
		top: 120px;
		left: 0;

		// visibility: hidden;

		height: calc(100vh - 80px);
		margin-top: 0;
		padding: 0;


		transform-origin: top;

		transform: scaleY(0);
		transition: transform .3s;

		a {
			position: relative;
			display: block;
			padding: 10px 30px 10px 0;

			color: $white;
			border-bottom: 1px solid rgba(255,255,255,.3);
		}

		&.visible {
			transform: scaleY(1);
			overflow: auto;
			transition: transform .3s;
			background: linear-gradient(0deg, rgba(0,0,0,.2), rgba(0,0,0,.2)), url('/assets/ilight/images/ilight-bg.jpg') no-repeat center top; // background image called from images folder);
			background-size: cover;
		}

		li {
			ul {
				visibility: hidden;

				max-height: 0;

				opacity: 0;
			}

			.show {
				visibility: visible;

				max-height: 100%;
				padding-left: 15px;

				opacity: 1;
			}
		}

		&__lvl1-item-link {
			padding: 15px 0;
		}
	}

	.plus-minus-toggle {
		position: absolute;
		top: 0;
		right: 0;

		width: 30px;
		height: 100%;

		cursor: pointer;

		&:before,
		&:after {
			position: absolute;
			top: 18px;
			left: 8px;

			width: 14px;
			height: 2px;

			content: '';
			transition: transform 500ms ease;

			background: $white;
		}
		&:after {
			transform: rotate(-270deg);
			transform-origin: center;
		}
		&:before {
			transform: rotate(-180deg);
		}
		&.active {
			&:after,
			&:before {
				transform: rotate(0deg);
			}
		}
	}
}

// desktop styling
@include desktop() {
	.main-menu {
		min-height: 55px;

		text-transform: uppercase;

		a {
			display: flex;

			text-decoration: none;

			color: $white;

			align-items: center;

			.icon-arrow {
				font-size: 1.4em;

				position: absolute;
				right: 0;

				cursor: pointer;

				@include rotate(-90deg);
			}
		}

		&__lvl1 {
			display: flex;

			align-items: center;

			&.hide {
				opacity: 0;
				visibility: visible;
			}
		}

		.main-menu__lvl1-item {
			position: relative;

			padding: 0 3px;

			&:first-child {
				a {
					height: 55px;
				}
			}

			&:hover {
				.main-menu__lvl2 {
					visibility: visible;

					opacity: 1;

					@include transition('visibility, opacity', 200ms);
				}
			}
		}

		.main-menu__lvl2 {
			position: absolute;
			z-index: 1;
			left: 0;

			width: 280px;

			background: $grey;

			@include box-shadow(2px, 2px, 5px, 0px, rgba(0, 0, 0, .5));

			li {
				padding: 0 20px;

				background: $black;
			}

			a {
				font-size: calc-em(16px, 18px);

				padding: 10px 20px 10px 10px;

				color: $white;
				border-bottom: 1px solid #555;

				&:hover {
					color: $grey;
				}
			}

			&-item:last-of-type {
				a {
					border-bottom: none;
				}
			}
		}

		.main-menu__lvl2-item {
			position: relative;

			&:hover {
				.main-menu__lvl3 {
					display: block;
					visibility: visible;

					opacity: 1;

					@include transition('visibility, opacity', 200ms);
				}
			}
		}

		.main-menu__lvl3 {
			position: absolute;
			top: 0;
			left: 100%;
			left: 100%;

			@include box-shadow(2px, 2px, 5px, 0px, rgba(0, 0, 0, .5));

			.icon-arrow {
				@include rotate(0);
			}

			&-item:last-of-type {
				a {
					border-bottom: none;
				}
			}
		}

		.main-menu__lvl4 {
			a {
				padding-right: 0;
			}
		}
	}
}
