// form styles

.container {
  width: auto !important;
}

.help-block {
  color: $white !important;
  @include font-family('book');
}

.radio {
  margin: 0 !important;

  label {
      color: $white;
  }
}

.body {
  .container {
    width: auto;
  }
}

.form-page {
  .container {
    padding: 0;
  }

  .standard-banner {
    &__content {
      bottom: -85px;
    }

    &__bg {
      height: 350px;
    }
  }
}

.sitecore-form.container {
  padding-top: 160px;
  padding-bottom: 70px;
  width: auto;

  &.rate-website-form {
    .form-group {

      flex-direction: column;

      .control-label {
        margin-bottom: 1em;
      }

      .radio {
        padding-bottom: .5em;
      }

    }

    .form-submit-border {
      padding-left: 0;
    }
  }

  .has-error {
    .list-group {
      margin: 0;
    }
  }

  h2 {
    font-size: 1.125em;
    letter-spacing: .2em;
    text-transform: uppercase;
    margin-top: 0;
    margin-bottom: 1.5em;
    font-size: 1.125em;
  }

  .subpages__header {
    margin-bottom: 1.5em;
    span {
      font-size: 1em;
      line-height: 1.375em;
    }
  }

  .form-group {
    margin-bottom: 1.5em;
    display: flex;
    align-items: flex-start;

    .control-label {
      @include font-family('light');
      text-transform: uppercase;
      width: 200px;
      font-size: 1em;
      letter-spacing: .1em;
      margin-bottom: 0;
      color: $white;
    }

    .form-control {
      padding: 0.4em .5em 0.4em;
      width: 50%;
      background: transparent;
      border: 1px solid $white;
    }
  }

  .btn {
    padding: 8px;
    border: none;
    color: #000;
    font-family: 'Hind', sans-serif;
    font-weight: 600;
    font-size: 0.8888888889em;
    outline: none;
    overflow: hidden;
    border-radius: 100px;
    width: 180px;
    display: inline-block;
    background: #eda91e;
    background: -webkit-linear-gradient(45deg, #eda91e 0%, #a6b721 100%);
    background: linear-gradient(45deg, #eda91e 0%, #a6b721 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#eda91e', endColorstr='#a6b721',GradientType=1 );

    text-transform: uppercase;
  }

  .form-submit-border {
    padding-left: 200px;
  }
}

@include mobile-tablet {
  .form-page {
    padding-top: 120px;
  }

  .sitecore-form.container {
    padding: 80px 15px 40px;

    .form-group {
      flex-direction: column;

      .control-label {
        padding-bottom: 1em;
      }

      .form-control {
        width: 100%;
      }
    }

    .form-submit-border {
      padding-left: 0;
    }
  }
}
