/* ==============================================
>> Mobile menu Hamburger icon
================================================= */
.mobile-menu-icon-container {
  position: absolute;
  top: 20px;
  right: 15px;
  width: 40px;
  height: 40px;
  cursor: pointer;
}

.mobile-menu-icon {
  position: relative;
  right: 8px;
  display: block;
  width: 21px;
  height: 17px;
  margin: 0 auto;
  padding: 0;
  cursor: pointer;
  transition: .5s ease-in-out;
  border: none;
  outline: none;
  background: none;
  transform: rotate(0deg);
  position: absolute;
  top: calc(50% - .5px);
  transform: translateY(-50%);
  -webkit-backface-visibility: hidden;
}

.mobile-menu-icon span.line, .mobile-menu-icon span.line::before, .mobile-menu-icon span.line::after {
  position: absolute;
  top: 9px;
  left: 0;
  display: block;
  width: 100%;
  height: 3px;
  transition: .25s ease-in-out;
  opacity: 1;
  border-radius: 3px;
  background: #fff;
  transform: rotate(0deg);
}

.mobile-menu-icon span.line::before {
  top: -7px;
  display: block;
  content: '';
  transform-origin: left center;
}

.mobile-menu-icon span.line::after {
  top: 7px;
  display: block;
  content: '';
  transform-origin: left center;
}

.is-active .mobile-menu-icon span.line::before {
  top: -7px;
  left: 3px;
  width: 100%;
  opacity: 1;
  transform: rotate(45deg);
}

.is-active .mobile-menu-icon span.line {
  background: transparent;
}

.is-active .mobile-menu-icon span.line::after {
  top: 8px;
  left: 3px;
  width: 100%;
  opacity: 1;
  transform: rotate(-45deg);
}

