// installations-section styles

$width: 522px;
$height: 360px;

@include largedesktop {
  .installations-section {
    .installation__image {
      width: 580px;
      height: 400px;
    }
  }
}

@include desktop {
  .installations-section {
    .installation.reverse {
      .installation__content {
        padding-left: 125px;
      }
    }
  }
}

.installations-section {
  padding: 0px 0 140px;

  &__header {
    position: absolute;
    z-index: 1;
    display: flex;
    top: 250px;

    h1 {
      font-size: calc-em(70px);
      @include font-family('demi');
      letter-spacing: .2em;
      margin: 0;
      padding-left: 20px;

      span {
        display: block;
        position: relative;
        line-height: 1;
        padding-bottom: 20px;

        &:nth-of-type(2) {
          left: 280px;
        }
        &:nth-of-type(3) {
          left: 130px;
        }
      }
    }

    .line {
      @include line(130px);
      position: relative;
      top: 24px;
    }
  }

  .installation {
    margin-bottom: 90px; // large value to incorporate offset for parallax

    &.reverse {
      flex-direction: row-reverse;

      .installation__image {
        margin-right: auto;
        margin-left: 55px;
      }
    }

    &__image {
      width: $width;
      height: $height;
      margin-left: auto;
      margin-right: 55px;
      box-shadow: 10px 7px 20px rgba(0,0,0,.4);
      overflow: hidden;
      position: relative;


      @include desktop {
        &:hover {
          img {
            transform: scale(1.1);
            transition: transform .3s;
          }
        }
      }

      img {
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        transform: scale(1);
        transition: transform .3s;
      }


      &.smaller {
        width: $width * .85;
        height: $height * .85;
      }
    }

    &__bg {
      justify-content: flex-end;
    }

    &__content {

      .title {
        @include font-family('demi');
        font-size: calc-em(28px);
        margin-bottom: .5em;
      }

      .name {
        @include font-family('demi');
        font-size: calc-em(18px);
      }

      .desc {
        @include font-family('light');
        font-size: calc-em(18px);
      }
    }
  }
}

@include mobile-tablet {
  .installations-section {
    padding-top: 30px;
    padding-bottom: 0;

    &__header {
      position: relative;
      top: 0;

      .line {
        @include line(50px);
      }

      h1 {
        span {
          &:nth-of-type(2) {
            left: 110px;
          }
          &:nth-of-type(3) {
            left: 0;
          }
        }
      }
    }

    .installation {

      &.reverse {
        .installation {

          &__image {
            margin-left: auto;
            margin-right: auto;
          }

          &__content {
            padding-left: 0;
          }

        }
      }

      &__image {
        width: 100%;
        height: 280px;

        &.smaller {
          width: 100%;
        }

        img {
          width: 100%;
        }
      }
    }
  }
}

@include mobile {
  .installations-section {

    .installation {
      margin-bottom: 40px;
    }

    &__header {
      h1 {
        font-size: calc-em(50px);
      }
    }

    .installation {
      &__content {
        .title {
          font-size: calc-em(24px);
        }

        .name {
          font-size: calc-em(18px);
        }

        .desc {
          font-size: calc-em(18px);
        }

        .readmore {
          font-size: calc-em(16px);
        }
      }

      &__image {
        height: 220px;

        &.smaller {
          height: 220px;
        }
      }
    }
  }
}
