// Normalize Styles
@import 'node_modules/normalize.css/normalize';

// Import Node Module Styles
@import 'node_modules/malihu-custom-scrollbar-plugin/jquery.mCustomScrollbar';
@import 'node_modules/slick-carousel/slick/slick.scss';
@import 'node_modules/slick-carousel/slick/slick-theme.scss';
@import 'node_modules/@fancyapps/fancybox/dist/jquery.fancybox.min';
@import 'node_modules/pikaday/scss/pikaday';
@import 'node_modules/aos/dist/aos';
@import 'node_modules/jquery-ui-bundle/jquery-ui.min';

// Fonts
@import url('https://fonts.googleapis.com/css?family=Hind:300,400,600');

// Import Common
@import 'common/variables';
@import 'common/mixins';
@import 'common/config';
@import 'common/grids';
@import 'common/icons';
@import 'common/helpers';

// Box sizing partial styles:
// Apply a natural box layout model to all elements
*,
*:before,
*:after {
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
}

/* =======================================================================
01 Global
========================================================================== */

@-ms-viewport {
	width: auto;
}

html {
	font-size: 1em;
	overflow-x: hidden;
}

body {
	// font-size: 1em; //16px
	font-size: 16px !important;
	font-weight: $thin;
	line-height: 1.6;

	position: relative;

	width: 100%;
	min-width: 300px;
	margin: 0;
	padding: 0;

	animation: load .5s;

	color: $white;

	@include font-family('book');

	&.grey-bg {
		background: $grey-light;
	}

	&.pagemode-edit {
		.header {
			position: static;
		}
	}

}

@-webkit-keyframes load {
	0% {
		opacity: 0;
	}
	20% {
		opacity: 0;
	}
}
@-moz-keyframes load {
	0% {
		opacity: 0;
	}
	20% {
		opacity: 0;
	}
}
@-o-keyframes load {
	0% {
		opacity: 0;
	}
	20% {
		opacity: 0;
	}
}
@keyframes load {
	0% {
		opacity: 0;
	}
	20% {
		opacity: 0;
	}
}

%plainlist {
	@include plainlist();
}

ul {
	@extend %plainlist;
}

a {
	@include transition('color, background-color, border', 300ms, linear);
	-webkit-backface-visibility: initial;

	color: $white;
	text-decoration: none;
}

h1,
h2,
h3 {
	font-weight: $bold;
	line-height: inherit;
}

h1 {
	font-size: calc-em(24px);
}
h2 {
	font-size: calc-em(20px);
}
h3 {
	font-size: calc-em(18px);
}

img {
	max-width: 100%;

	vertical-align: initial;
}

// Styling on warning for users on IE7 or below
.browsehappy {
	margin: 0;
	padding: .5em 0;

	text-align: center;

	color: #000;
	background: #fff8e2;
}

// Normalise fieldset
fieldset {
	margin: 0;
	padding: 0;

	border: 0;
}

.clear {
	clear: both;
}

.shooting-star {
	background-image: url('/assets/ilight/images/shooting-star.png');
	background-size: contain;
	background-repeat: no-repeat;
	height: 1.4em;
	width: 200px;
	display: inline-block;

	position: relative;
	left: -5px;
}

.shine {
	text-shadow: 0px 5px 30px rgba(255, 255, 255, 0.75);
}

.no-transform {
	transform: none !important;
}

.standard-content {
	padding: 140px 70px 60px;
}

.home-content {
	padding-left: 50px;
	position: relative;

	&:before {
		content: '';
		position: absolute;
		top: -50px;
		right: 0;
		height: 600px;
		width: 60%;
		background-image: url('/assets/ilight/images/galaxy2.png');
		background-size: cover;
		opacity: .3;
	}
}

.space-left {
	padding-left: 70px;
}

/* =======================================================================
02 Layout
========================================================================== */

.wrapper {
	position: relative;

	overflow-x: hidden;

	background: #fff;

	@include transition(transform, 200ms, ease-in-out);
}

%container {
	position: relative;

	max-width: 1440px;
	margin: 0 auto;
	padding: 0 20px;
}

.container {
	width: auto;
	margin: 0 auto;

	@extend %container;
	@include mobile {
		padding: 0 15px;
	}

	&--narrow {
		max-width: 960px;
	}

	&--large {
		max-width: 1440px;
	}
}

#main-content {
	min-height: 500px;
	background-color: $dark-purple;

	&.font-normal {
		font-size: 1em;
	}
	&.font-large {
		font-size: 1.125em;
	}
	&.font-larger {
		font-size: 1.25em;
	}
}

.standard-body-wrapper {

	.container {
		width: auto;
	}

	&.detail-page {
		background-image: url('/assets/ilight/images/standard-body-bg.png');
		background-size: cover;
		background-repeat: no-repeat;
	}

	.body {
		width: 100%;
		min-height: 600px;
	}
}

