/*========================================================================
>> Common for all the pages
========================================================================== */
select.dd-select::-ms-expand {
	display: none;
}

select.dd-select {
	padding: 3px 30px 3px 10px !important;

	border: none;
	border-radius: 0;
	background-color: $gold-light;
	background-image: url(/Cwp/assets/ffw/images/arrow_drop_down.svg); // background image called from images folder
	background-repeat: no-repeat;
	background-position: right 5px center;
	background-size: 25px 25px;

	-moz-appearance: none;
	-webkit-appearance: none;
	appearance: none;
}

button,
input,
select {
	&:focus {
		outline: none;
	}
}

.object-fit {
	width: 100%;
	height: 100%;

	object-fit: cover;
}

// Malihu custom scroller colors
.mCSB_scrollTools .mCSB_dragger .mCSB_dragger_bar,
.mCSB_scrollTools .mCSB_dragger:hover .mCSB_dragger_bar,
.mCSB_scrollTools .mCSB_dragger:active .mCSB_dragger_bar,
.mCSB_scrollTools .mCSB_dragger.mCSB_dragger_onDrag .mCSB_dragger_bar {
	background-color: $green;
}

.mCSB_scrollTools .mCSB_draggerRail {
	background-color: $grey;
}

/* =======================================================================
04 Mediaqueries
========================================================================== */
@include mobile-tablet {
	.home-content {
		padding-left: 5px;
		padding-right: 5px;
	}
	.handheld-only {
		display: block;
	}

	.desktop-only {
		display: none;
	}

	.white-bg-container {
		padding: 0 20px 40px;
	}

	.standard-body-wrapper {
		.container {
			padding: 0;
		}
	}

	.space-left {
		padding-left: 0;
	}
}

@include desktop {
	#main-content {
		margin-top: 77px; // Shift up to offset nav

		&.home {
			margin-top: 0;
		}
	}
	.handheld-only {
		display: none;
	}

	.desktop-only {
		display: block;
	}
}

@include mobile {
	.standard-content {
		padding: 0 15px 0;
	}
}
