// detail-body styles
.detail-content {
  padding: 140px 0 70px 60px;

  &__slick-video {
    position: relative;
    outline: none;

    .overlay {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(0,0,0,.5);

      display: flex;
      align-items: center;
      justify-content: center;

      span {
        font-size: 4em;
      }
    }

    &:hover {
      .icon {
        font-size: 4.5em;
        transition: font-size .3s;
      }
    }
  }

  &__video {
    height: 450px;
    margin: 70px 0;
  }

  &__overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,.7);

    display: flex;
    align-items: center;
    justify-content: center;

    &:hover {
      .icon {
        transform: scale(1.1);
        transition: transform .3s;
      }
    }

    .icon {
      font-size: 5em;
      transform: scale(1);
      transition: transform .3s;
    }
  }

  &__play {
    height: 100%;
    display: flex;
    background-image: url('/assets/ilight/images/detail-video-bg.png');
    background-size: cover;
    background-position: center;
    position: relative;
    outline: none;

    img {
      opacity: 0;
      visibility: hidden;
    }
  }

  &__slick {
    width: 90%;
    margin: 0 auto;
    margin-right: 0;
  }

  .slick-dotted.slick-slider {
    margin-bottom: 0;
  }

  &__image {
    height: 400px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }

  &__gallery {
    // width: 700px;

    .slick-arrow {
      height: 45px;
      width: 45px;
      z-index: 2;

      &:before {
        font-size: 45px;
      }
    }

    .slick-prev {
      left: 12px;
    }

    .slick-next {
      right: 11px;
    }

    .slick-dots {
      bottom: 25px;

      li {
        button {
          height: 10px;
          width: 10px;
          border-radius: 10px;
          border: 1px solid $white;
          background-color: transparent;


          &:before {
            content: none;
          }
        }

        &.slick-active {
          button {
            background-color: $white;
          }
        }
      }
    }
  }

  &__about {
    .title {
      @include font-family('demi');
      font-size: calc-em(28px);
      margin-bottom: 10px;
      margin-top: 0;
    }

    .desc {
      @include font-family('light');
      font-size: calc-em(18px);
      margin-bottom: 0;
    }
  }

  &__detail {
    display: flex;
    padding-top: 20px;
  }

  &__video {
    display: flex;
    position: relative;
  }

  &__header {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    display: flex;
    flex-direction: column;
    align-items: flex-end;

    .line {
      @include line(150px);
      position: relative;
      left: 0;
    }

    h1 {
      @include font-family('demi');
      font-size: calc-em(60px);
      margin: 0;
      letter-spacing: .2em;

      span {
        display: block;
        position: relative;
        line-height: 1;
        padding-bottom: 15px;
        right: 115px;

        &:last-of-type {
          right: 40px;
        }
      }
    }
  }

  &__about {
    .col-4-md {
      display: flex;
      justify-content: center;

      img {
        width: 270px;
      }
    }
  }

  &__dp-wrap {
    .slick-prev {
      left: 70px;
    }
    .slick-next {
      right: 70px;
    }
    .slick-arrow {
      height: 30px;
      width: 30px;
      z-index: 2;

      &:before {
        font-size: 30px;
      }
    }
    .slick-list {
      width: 270px;
    }
  }
}

@include mobile-tablet {
  .detail-content {
    &__gallery {
      .slick-arrow {
        &:before {
          opacity: 1;
        }
      }
    }
  }
}

@include mobile {
  .detail-content {
    padding: 0 15px 0;

    &__about {
      padding-top: 60px;

      .col-8-md {
        padding: 30px 35px 70px;
      }
    }

    &__play {
      height: 270px;
    }

    &__video {

      .col-8-md {
        display: flex;
        flex-direction: column-reverse;

        a {
          height: 270px;
          width: 100%;
          margin-top: 40px;
          margin-left: 5px;
          margin-right: 5px;
        }
      }
    }

    &__header {
      position: relative;
      transform: none;
      width: 90%;
      top: 0;
      right: 0;
      align-self: flex-end;

      .line {
        width: 80%;
      }
    }

    &__gallery {
      .slick-dots {
        bottom: -25px;
      }
    }

    &__detail {
      flex-direction: column-reverse;
    }

    &__slick {
      width: 100%;
    }

    &__image {
      height: 250px;
    }
  }
}
