$letter_spacer: 50px;

.home-banner {
	position: relative;

	&:after {
		content: '';
		position: absolute;

		height: 200px;
		width: 100%;
		bottom: 0;
		z-index: 2;

		/* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#181b31+0,181b31+100&0+0,1+100 */
		background: -moz-linear-gradient(top, rgba(24,27,49,0) 0%, rgba(24,27,49,1) 100%); /* FF3.6-15 */
		background: -webkit-linear-gradient(top, rgba(24,27,49,0) 0%,rgba(24,27,49,1) 100%); /* Chrome10-25,Safari5.1-6 */
		background: linear-gradient(to bottom, rgba(24,27,49,0) 0%,rgba(24,27,49,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
		filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00181b31', endColorstr='#a6181b31',GradientType=0 ); /* IE6-9 */
	}
}

#bridges_of_time {
	width: 415px;
	height: 104.11px;
	opacity: 0;
	visibility: hidden;

	&.show {
		opacity: 1;
		visibility: visible;
	}
}

.silhouette {
	position: absolute;
	left: 0;
	right: 0;
	width: 100%;
	background: url('/assets/ilight/images/silhouette.png') no-repeat center top; // background image called from images folder);
	background-size: cover;
	height: 400px;
	z-index: -1;
	bottom: 0;
	opacity: 0;
}

.gradient {
	position: absolute;
	left: 0;
	right: 0;
	height: 100%;
	width: 100%;

	/* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#070808+0,f8a78e+100&0.65+0,1+100 */
	background: -moz-linear-gradient(top, rgba(7,8,8,0.65) 0%, rgba(248,167,142,1) 100%); /* FF3.6-15 */
	background: -webkit-linear-gradient(top, rgba(7,8,8,0.65) 0%,rgba(248,167,142,1) 100%); /* Chrome10-25,Safari5.1-6 */
	background: linear-gradient(to bottom, rgba(7,8,8,0.65) 0%,rgba(248,167,142,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#a6070808', endColorstr='#f8a78e',GradientType=0 ); /* IE6-9 */
	z-index: 0;
	opacity: .2;
}

.galaxy1 {
	position: absolute;
	left: 0;
	width: 100%;
	background: url('/assets/ilight/images/galaxy1.png') no-repeat center top; // background image called from images folder);
	background-position: left;
	background-size: contain;
	height: 400px;
	z-index: 2;
	top: 40px;
	opacity: .3;
}

.tall-canvas {
	text-align: center;
	position: absolute;
	height: 150vh;
	top: 0;
	left: 0;
	width: 100%;

	background: url('/assets/ilight/images/ilight-bg.jpg') no-repeat center top; // background image called from images folder);
	background-size: cover;

	.canvas {
		height: 100vh;
		position: sticky;
		top: 0;
		left: 0;
		width: 100%;

		display: flex;
		align-items: center;
		flex-direction: column;
		justify-content: center;

		padding-left: 70px;

		z-index: 3;

		.admission {
			@include font-family('light');
			font-size: calc-em(20px);

			margin: 0;
		}

		&__timing {
			@include font-family('light');
			font-size: calc-em(18px);

			display: flex;
			align-items: center;
			justify-content: center;
			text-transform: uppercase;

			p {
				padding: 0 25px;

				&:first-of-type {
					border-right: 1px solid $white;
				}
			}
		}

		&__parallax {
			display: flex;
			align-items: center;
			justify-content: center;
			flex-direction: row;
		}

		&__delayappear {
			opacity: 0;
			visibility: hidden;
			transform: translateY(50px);

			&.show {
				opacity: 1;
				visibility: visible;
				transform: translateY(0);
				transition: all .7s;
			}
		}

		&__explorenow {
			padding: 8px;
			border: none;
			color: $black;
			@include font-family('demi');
			font-size: calc-em(18px);
			outline: none;
			overflow: hidden;
			border-radius: 100px;
			width: 180px;
			margin-top: 15px;
			display: inline-block;

			transition: filter .3s;
			filter: brightness(1);

			/* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#eda91e+0,a6b721+100 */
			background: rgb(237,169,30); /* Old browsers */
			background: -moz-linear-gradient(45deg, rgba(237,169,30,1) 0%, rgba(166,183,33,1) 100%); /* FF3.6-15 */
			background: -webkit-linear-gradient(45deg, rgba(237,169,30,1) 0%,rgba(166,183,33,1) 100%); /* Chrome10-25,Safari5.1-6 */
			background: linear-gradient(45deg, rgba(237,169,30,1) 0%,rgba(166,183,33,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
			filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#eda91e', endColorstr='#a6b721',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */

			&:hover {
				transition: filter .3s;
				filter: brightness(1.3);
			}
		}

		.slogan {
			@include font-family('light');
			font-size: calc-em(24px);
			margin-bottom: .35em;
		}

		.lightup {
			text-align: center;
			position: relative;
			top: 130px;

			h2 {
				display: inline-block;
				position: relative;
				color: #fff;
				text-shadow: 0px 0px 0px rgba(255, 255, 255, 0.75);
				transition: text-shadow 0.4s;
				font-size: calc-em(87.54px);
				letter-spacing: .2em;
				user-select: none;

				@include font-family('demi');

				&.shine {
					transition: text-shadow 0.4s;
					text-shadow: 0px 0px 50px rgba(255, 255, 255, 0.75);
				}

				&.static {
					top: 0 !important;
				}
			}

			h3 {
				color: #FFDB58;
				visibility: hidden;
				opacity: 0;
				transition: all .3s;
				transform: translateY(0);

				&.show {
					visibility: visible;
					opacity: 1;
					transform: translateY(-60px);
					transition: all .3s;
				}
			}

			#i {
				top: 50px;
			}

			#l {
				top: 100px;
				margin-left: $letter_spacer;
			}

			#i2 {
				top: 20px;
			}

			#g {
				top: 50px;
			}

			#h {
				top: 30px;
			}

			#t {
				top: 110px;
			}

			#s {
				top: 70px;
				margin-left: $letter_spacer;
			}

			#i3 {
				top: 30px;
			}

			#n {
				top: 100px;
			}

			#g2 {
				top: 10px;
			}

			#a {
				top: 50px;
			}

			#p {
				top: 30px;
			}

			#o {
				top: 45px;
			}

			#r {
				top: 90px;
			}

			#e {
				top: 60px;
			}
		}

		&__scrollto {
			display: block;
			text-transform: uppercase;
			letter-spacing: 2px;
			position: absolute;
			bottom: 50px;
			left: 0;
			text-align: center;
			font-size: 1em;
			color: $white;
			width: 100%;
			transition: all .3s;

			a {
				cursor: default;
			}

			.icon {
				display: block;
				font-size: 1.5em;
			}

			&.hide {
				transition: all .3s;
				opacity: 0;
				visibility: hidden;
			}
		}
	}
}

.spacer {
	height: 150vh;
}

@media (max-width: 1110px) {
	.tall-canvas .canvas .lightup h2 {
		font-size: calc-em(78px);
	}
}

@include mobile-tablet {

	#bridges_of_time {
		width: 380px;
		height: 104px;
	}

	.galaxy1 {
		height: 200px;
		top: -15px;
	}

	.gradient {
		display: none;
	}

	.tall-canvas {
		height: 70vh;

		.canvas {
			flex-direction: column;
			height: 70vh;
			padding-left: 0;

			&__parallax {
				flex-direction: column;
				margin-top: 110px;
			}

			&__scrollto {
				display: none;
			}

			.lightup {
				width: 100%;
				transform: none !important;
				top: 0;

				h2 {
					margin: 0;
					transform: none !important;
				}

				#l {
					margin-left: 30px;
				}

				#s {
					margin-left: 0;
				}

			}
		}
	}

	.spacer {
		height: 70vh;
	}

}

@include mobile {
	#bridges_of_time {
		width: 230px;
		height: 57.69px;
		margin-top: 5px;
	}

	.spacer {
		height: 100vh;
	}

	.tall-canvas {
		height: 100vh;

		.canvas {
			height: 100vh;
			background-size: cover;

			&__parallax {
				margin-top: 50px;
			}

			.lightup {

				h2 {
					font-size: calc-em(49px);
					letter-spacing: .1em;
					user-select: none;
				}

				#l {
					margin-left: 15px;
				}
			}

			.slogan {
				font-size: calc-em(20px);
				max-width: 70%;
				margin-left: auto;
				margin-right: auto;
				user-select: none;
			}

		}
	}
}

@media (max-width: 480px) {
	.tall-canvas {
		.canvas__timing {
			flex-direction: column;

			p {
				padding: 0;
				margin: .25em 0 .25em;

				&:first-of-type {
					border-right: none;
				}
			}
		}
	}
}
