/*==============================
>>Search icon
================================ */
.mobile-search-icon-container {
  position: absolute;
  top: 20px;
  right: 65px;

  width: 40px;
  height: 40px;

  cursor: pointer;
  text-align: center;

  border: 1px solid $grey;

  &.is-active {
    background: $grey;
  }

  .mobile-search-icon {
    display: block;

    width: 100%;
    padding: 0;

    text-align: center;
    text-decoration: none;

    border: none;
    background: none;
  }
  .icon {
    font-size: 1.6em;
    line-height: 40px;

    display: inline-block;

    color: $green;
  }
}




