// media-listing styles
.media-listing {
  max-width: 80%;
  margin-left: auto;
  margin-right: auto;

  &:last-child {
    padding-top: 35px;
  }

  &-wrapper {
    padding-bottom: 70px;
  }

  &__viewmore {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 1em;
    @include font-family('demi');
    font-size: calc-em(16px);

    .icon {
      font-size: 1.5em;
      position: relative;
      top: -1px;
    }
  }

  &__header {
    @include font-family('demi');
    font-size: calc-em(50px);
    color: $white;
    letter-spacing: .2em;
  }

  li { // ul
    a {
      background-color: $dark-purple;
      display: flex;
      align-items: center;
      border-top: 1px solid #cbcbcb;
      padding: 1.5em;
    }

    &:nth-child(odd) {
      a {
        background-color: $pale-purple;
      }
    }
  }

  &__title {
    @include font-family('demi');
  }

  .readmore {
    @include font-family('demi');
    font-size: calc-em(16px);
    border: 1px solid $yellow;
    background-color: transparent;
    border-radius: 18px;
    padding: 5px 30px;
    text-transform: uppercase;
    display: inline-block;
    filter: brightness(1);
    transition: filter .3s;

    &:hover {
      filter: brightness(1.3);
      transition: filter .3s;
    }
  }

  &__readmore {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

@include mobile-tablet {
  .media-listing {
    max-width: 100%;

    &-wrapper {
      padding-left: 15px;
      padding-right: 15px;
    }
  }
}

@include mobile {
  .media-listing {
    &__readmore {
      justify-content: flex-start;
    }

    &__date {
      margin-top: 0;
    }
  }
}
