// footer-menu styles
.footer-menu {
  font-size: calc-em(16px);

  &__list {
    @extend %plainlist;
  }

  &__item {
    line-height: 1;

    display: inline-block;

    padding: 0 10px 0 5px;

    border-right: 1px solid $darkgrey;

    &:first-of-type {
      padding-left: 0;
    }

    &:last-child {
      padding-right: 0;

      border-right: none;
    }
  }

  &__link {
    text-decoration: none;

    color: $white;

    &:hover {
      color: $grey;
    }
  }
}
