// standard-banner styles
.standard-banner {
  color: $white;
  position: relative;

  &__bg {
    height: 450px;
    width: 100%;
    background-size: cover;
    display: flex;
    align-items: flex-end;
  }

  &__content {
    @include font-family('book');
    font-size: calc-em(22px);
    padding-left: 60px;
    position: absolute;
    bottom: -115px;

    h1 {
      font-size: calc-em(60px);
      letter-spacing: .2em;
      margin: 0;
      @include font-family('demi');

      span {
        display: block;
        line-height: 1;
        padding-bottom: 10px;

        &:last-of-type {
          position: relative;
          left: 80px;
        }
      }
    }

    p {
      margin: 0;
    }

    .subheader {
      margin-bottom: 15px;
    }

    .subtitle {
      margin-bottom: 10px;
      @include font-family('light');
    }

    .icon {
      font-size: .9em;
      @include font-family('light');

      margin-right: 5px;
    }

    .icon-calendar {
      margin-right: 7px;
    }
  }
}

@include mobile {
  .standard-banner {
    &__bg {
      height: 270px;
    }

    &__content {
      padding-left: 30px;
      position: relative;
      bottom: 0;
      margin-top: -135px;

      h1 {
        font-size: calc-em(50px);
        letter-spacing: .17em;

        span {
          padding-bottom: 15px;
        }
      }
    }
  }
}
