// map styles
$marker_size: 32px;
$circle_size: 22px;
$related_circle_size: 18px;
$legend_circle_size: 25px;

// map colors
$purple: #6123ed;
$hotel_blue: #287ea8;
$maroon: #8d08b3;
$teal: #01806d;
$dining_blue: #1862dc;
$brown: #7a0c16;
$pink: #c10896;
$brickred: #993b22;

$white_90: rgba(255,255,255,.9);


// dot js template
#mapdetails_receive {
  height: 100%;
  position: relative;
}

.mapinfo {
  &__text {
    width: 65%;
    padding-left: 10px;
    padding-right: 10px;
    color: $black;
  }
  &__number {
    width: 8%;

    .circle {
      width: $circle_size;
      height: $circle_size;
      border-radius: $circle_size;
      background-color: $purple;
      color: $white;

      position: relative;
      top: 3px;

      @include font-family('demi');
      font-size: calc-em(12.28px);

      &.installations {
        background-color: $purple;
      }

      &.programmes {
        background-color: $maroon;
      }

      &.dining {
        background-color: $dining_blue;
      }

      &.hotel {
        background-color: $hotel_blue;
      }

      &.retail {
        background-color: $teal;
      }

      span {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }
  }
  &__holder {
    position: relative;
    width: 80px;
    height: 60px;
    img {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      height: 100%;
      width: 100%;
    }
  }
  &__img {
    width: 27%;
    display: flex;

    align-items: flex-start;
    justify-content: center;
  }
  &__title {
    @include font-family('demi');
    font-size: calc-em(16px);
    margin-top: 0;
    margin-bottom: .3em;
  }
  &__artist {
    @include font-family('light');
    font-size: calc-em(14px);
    margin: 0;
  }
  &__location {
    @include font-family('light');
    font-size: calc-em(14px);
    margin: 0;
  }
  &__each {
    border-bottom: 1px solid #707070;
    display: flex;
    padding:  15px 0;
  }
}

.map {
  position: relative;
  height: 750px;

  &__detailed-mobile {
    display: none;
  }

  &__mobiletrigger {
    visibility: hidden;
    opacity: 0;
  }

  &__related {
    color: $black;
    padding: 10px 0 12px;
    margin-left: 20px;
    margin-right: 20px;
    margin-bottom: 5px;
    border-top: 1px solid rgba(0,0,0,.3);
    white-space: nowrap;
    position: absolute;
    bottom: 0;

    &-each {
      width: 50%;
      display: inline-block;
      padding: 0 3px;

      .text {
        @include font-family('light');
        font-size: calc-em(14px);
        width: 75%;
        white-space: normal;
      }

      .circle {
        width: $related_circle_size;
        height: $related_circle_size;
        border-radius: $related_circle_size;
        color: $white;

        position: relative;
        top: 6px;
        margin-right: 5px;

        display: inline-block;

        @include font-family('demi');
        font-size: calc-em(12.28px);

        &.installations {
          background-color: $purple;
        }

        &.programmes {
          background-color: $maroon;
        }

        &.dining {
          background-color: $dining_blue;
        }

        &.hotel {
          background-color: $hotel_blue;
        }

        &.retail {
          background-color: $teal;
        }

        span {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
        }
      }
    }

    &-img {
      height: 70px;
      overflow: hidden;
    }

    p {
      margin: 0;
      @include font-family('book');
      font-size: calc-em(16px);
      padding-bottom: 10px;
    }
  }

  &__desc {
    color: $black;
    padding: 12px 0 5px;
    margin-left: 20px;
    margin-right: 20px;
    @include font-family('light');
    font-size: calc-em(16px);

    p {
      margin: 0;
      line-height: 1.45em;
    }

    a {
      color: $black;
    }

    .button {
      margin-top: .8em;

      &__text {
        color: $black;
        font-size: calc-em(14px);
        padding-top: 4px;
        padding-bottom: 4px;
      }

      &__link {
        width: 150px;
      }
    }
  }

  &__bg {
    height: 190px;
    width: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: 0 -45px;

    .back {
      font-size: 2.5em;
      padding-left: 10px;
      padding-top: 10px;
      display: inline-block;
    }
  }

  &__text {
    padding-left: 10px;
    h1 {
      font-size: calc-em(18px);
      @include font-family('demi');
    }
    .shooting-star {
      width: 100px;
      margin-top: -1em;
      position: relative;
      left: -5px;
    }
    p {
      font-size: calc-em(14px);
      @include font-family('light');
      margin: 0;
    }
    .artist {
      margin-top: -15px;
    }
  }

  &__meta {
    background-color: $dark-purple;
    display: flex;
    padding: 10px 20px;

    .container {
      padding: 0;
      margin: 0;

      .circle {
        width: $circle_size;
        height: $circle_size;
        border-radius: $circle_size;
        border: 2px solid $white;
        position: relative;
        top: 3px;

        span {
          @include font-family('demi');
          font-size: calc-em(12.28px);
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
        }

        &.installations {
          background-color: $purple;
        }

        &.programmes {
          background-color: $maroon;
        }

        &.dining {
          background-color: $dining_blue;
        }

        &.hotel {
          background-color: $hotel_blue;
        }

        &.retail {
          background-color: $teal;
        }
      }
    }
  }

  &__detailed {
    height: 100%;
    width: 100%;
    background-color: #f6f6f6;
    position: absolute;
    z-index: 2;
    display: none;
    overflow-x: hidden;
  }

  &__info {
    height: calc(100% - 196px);
    background-color: #f3f4f5;
    margin-top: 1.8px;
    color: $black;

    &-scroll {
      height: 100%;
      padding: 0 10px;
      overflow-y: scroll;
      width: 95%;
      margin: 0 auto;

      position: relative;
      top: 10px;
      height: calc(100% - 20px);

      &::-webkit-scrollbar {
        -webkit-appearance: none;
      }

      &::-webkit-scrollbar:vertical {
        width: 5px;
      }

      &::-webkit-scrollbar:horizontal {
        height: 10px;
      }

      &::-webkit-scrollbar-thumb {
        background-color: #707070;
        border-radius: 10px;
      }

      &::-webkit-scrollbar-track {
        border-radius: 10px;
        background-color: #b7b7b7;
      }
    }
  }

  &__filters {
    padding: 15px;
    padding-left: 17px;
    height: 196px;

    &-each {
      .icon {
        font-size: 1.5em;
        padding-left: 5px;
      }

      .icon-flag {
        font-size: 1em;
      }
    }

    &.installations {
      background-color: $purple;
      color: $purple;
    }

    &.programmes {
      background-color: $maroon;
      color: $maroon;
    }

    &.dining {
      background-color: $dining_blue;
      color: $dining_blue;
    }

    &.hotel {
      background-color: $hotel_blue;
      color: $hotel_blue;
    }

    &.retail {
      background-color: $teal;
      color: $teal;
    }


    a {
      @include font-family('book');
      font-size: calc-em(16px);
      border: 1px solid $white;
      padding: 5px 12px;
      border-radius: 17px;

      display: inline-flex;
      align-items: center;
      justify-content: center;
      color: $white;

      &.active {
        background: $white;
        color: inherit;
      }
    }
  }

  &__top {
    padding-bottom: 1.2em;

    .icon-arrow_drop_down {
      display: none;
    }

    .map__filters-each {
      width: calc(50% - 5px);
      margin-right: 5px;
    }
  }

  &__bottom {
    padding-bottom: .3em;

    .icon {
      font-size: 1em;
      padding-left: 5px;
    }

    a {
      width: calc(33.3% - 5px);

      &:nth-of-type(1) {
        margin-right: 5px;
      }
      &:nth-of-type(2) {
        margin-right: 5px;
      }
    }
  }

  &__infowindow {
    position: absolute;
    left: 20px;
    top: 20px;
    height: calc(100% - 40px);
    width: 29%;

    h1 {
      @include font-family('demi');
      font-size: calc-em(20px);
      margin: 0;
      margin-bottom: .5em;
      color: $white;
    }
  }

  &__suggestedroutes {
    position: absolute;
    bottom: 23px;
    right: 70px;
    background-color: $white_90;

    display: flex;
    flex-direction: row;

    .close {
      display: none;
    }

    h1 {
      color: $black;
      padding-right: 1.5em;
      margin-left: 1.5em;
      @include font-family('demi');
      font-size: calc-em(20px);

      &:after {
        content: '\e90f';
        font-family: 'icomoon';
      }
    }

    ul {
      display: flex;
      align-items: center;
      li {
        margin-right: 1em;
        a {
          border: 1px solid rgba(0,0,0,.3);
          color: rgba(0,0,0,.6);
          @include font-family('book');
          font-size: calc-em(16px);
          padding: 4px 15px;
          border-radius: 20px;
          transition: all .2s;

          &.active {
            color: $white;
            border: 1px solid transparent;
            transition: all .2s;
          }
        }

        a[data-show="A"].active {
          background-color: $brickred;
        }

        a[data-show="B"].active {
          background-color: $pink;
        }

        a[data-show="C"].active {
          background-color: $brown;
        }

        &:last-child {
          margin-right: 1.5em;
        }
      }
    }
  }

  #map {
    height: 100%;
    width: 100%;
  }

  .route-marker {
    background-image: url('/assets/ilight/images/route.svg');
    background-size: cover;
    position: absolute;
    background-repeat: no-repeat;
    width: 500px;
    height: 444px;
  }

  .marker {
    position: absolute;
    cursor: pointer;
    width: $marker_size;
    height: $marker_size;
    border-radius: $marker_size;
    background: red;
    visibility: visible;
    opacity: 1;
    z-index: 1000;

    display: flex;
    align-items: center;
    justify-content: center;
    @include font-family('demi');
    font-size: calc-em(21.6px);

    &.glow {
      border: 1px solid rgba(255, 255, 255, .4);
      box-shadow: 0px 5px 5px rgba(255, 255, 255, 0.05) inset, 0px 0px 20px rgba(255, 255, 255, 0.8);
    }

    &.hide {
      opacity: 0;
      visibility: hidden;
    }

    &.installations {
      background-color: $purple;
    }
    &.programmes {
      background-color: $maroon;
    }
    &.hotel {
      background-color: $hotel_blue;
    }
    &.retail {
      background-color: $teal;
    }
    &.dining {
      background-color: $dining_blue;
    }
  }
}

.legend {
  background-color: #1a3343;
  min-height: 100px;
  margin: 1.8em 0;
  padding: 5px 30px;

  &__legend {
    display: flex;
    align-items: center;

    span {
      @include font-family('demi');
      font-size: calc-em(18px);
      width: 120px;
    }

    &-items {
      width: calc(100% - 120px);

      .row {
        display: flex;
      }

      .each {
        display: flex;
        align-items: center;
        flex-direction: row;
        width: 192px;

        .circle {
          width: $legend_circle_size;
          height: $legend_circle_size;
          border-radius: $legend_circle_size;
          border: 2px solid $white;
          margin-right: 10px;

          &.installations {
            background-color: $purple;
          }

          &.programmes {
            background-color: $maroon;
          }

          &.dining {
            background-color: $dining_blue;
          }

          &.hotel {
            background-color: $hotel_blue;
          }

          &.retail {
            background-color: $teal;
          }
        }

        img {
          height: 25px;
          margin-right: 10px;
        }
        p {
          @include font-family('book');
          font-size: calc-em(16px);
        }
      }
    }


  }

  &__suggestedroutes {
    border-bottom: 1px solid rgba(255,255,255,.4);
    display: flex;
    align-items: center;

    span {
      @include font-family('demi');
      font-size: calc-em(18px);
      margin-right: 120px;
    }

    ul {
      display: flex;
      flex-direction: row;

      li {
        display: flex;
        flex-direction: row;
        align-items: center;
        margin-right: 80px;

        p {
          @include font-family('book');
          font-size: calc-em(16px);
        }
      }
    }

    .bar {
      height: 10px;
      width: 70px;
      margin-right: 1em;

      &.A {
        background-color: $brown;
      }

      &.B {
        background-color: $pink;
      }

      &.C {
        background-color: $brickred;
      }
    }
  }
}

// remove default google maps widgets
.gm-svpc,
.gm-fullscreen-control,
.gm-style-mtc {
  display: none;
}

@include mobile-tablet {
  $infowindow_height: 550px;
  $mobiletrigger: 40px;

  #mapdetail_mobile-receive {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding-top: 20px;

    .map__bg {
      height: 70px;
      width: 100px;
      border-radius: 0;
      background-position: center;
    }

    .map__meta {
      background-color: transparent;

      h1 {
        text-decoration: underline;
        color: $black;
      }

      .container {
        .circle {
          width: 25px;
          height: 25px;
          border-radius: 25px;
          margin-right: 5px;
          top: 0;
        }
      }
    }

    .map__meta {
      padding-top: 0;
    }

    .map__text {
      .wrap {
        display: flex;
      }
      p {
        color: $black;
      }
    }
  }

  .map {
    height: 600px;

    &__detailed-mobile {
      display: none;
      position: relative;

      &.show {
        display: block;
        padding-left: 20px;
        padding-right: 20px;
      }

      .back {
        position: absolute;
        color: $black;
        font-size: 2.2em;
        top: 14px;
        left: 15px;
      }
    }

    &__top {
      .icon-arrow_drop_down {
        display: inline-block;
        color: $black;
        position: absolute;
        width: auto;
        font-size: 2em;
        top: 3px;
        right: 0;
        border: none;
      }
    }

    &__mobiletrigger {
      opacity: 1;
      visibility: visible;

      height: $mobiletrigger;
      width: $mobiletrigger;
      border-radius: $mobiletrigger;
      background-color: $white_90;
      position: absolute;
      top: 30px;
      right: 30px;

      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;

      span {
        color: $black;
        font-size: 1.3em;
      }
    }

    &__suggestedroutes {
      bottom: auto;
      top: 30px;
      right: 30px;
      opacity: 0;
      visibility: hidden;
      transition: all .3s;

      flex-direction: column;
      padding: 15px 20px;
      border-radius: 5px;

      &.show {
        opacity: 1;
        visibility: visible;
        transition: all .3s;
      }

      .close {
        color: $black;
        display: inline-block;
        position: absolute;
        top: 10px;
        right: 15px;
        font-size: 1em;
      }

      h1 {
        margin-left: 0;
        margin-top: 0;
        padding-right: 0;
      }

      ul {
        margin-left: 0;

        li {
          &:last-child {
            margin-right: 0;
          }
        }
      }
    }

    &__related {
      position: relative;
    }

    &__bg {
      border-top-left-radius: 10px;
      border-top-right-radius: 10px;
    }

    &__info {
      height: $infowindow_height - 196px;
    }

    &__infowindow {
      max-height: 51px;
      height: auto;
      overflow-y: hidden;
      width: calc(100% - 30px);
      left: 15px;
      right: 15px;
      top: auto;
      bottom: 0;
      cursor: pointer;
      transition: max-height .5s;

      background-color: $white_90;

      border-top-left-radius: 10px;
      border-top-right-radius: 10px;

      z-index: 1;

      h1 {
        color: $black;
      }

      &.preview {
        max-height: 150px;

        .map__filters {
          display: none;
        }

        .map__info {
          display: none;
        }
      }

      &.show {
        max-height: 1000px;
        transition: max-height .5s;

        .map__top {
          .icon-arrow_drop_down {
            transform: rotate(180deg);
            color: $white;
          }
        }

        .map__filters {

          &.installations {
            transition: background-color .3s;
            background-color: $purple;
          }

          &.programmes {
            transition: background-color .3s;
            background-color: $maroon;
          }

          &.dining {
            transition: background-color .3s;
            background-color: $dining_blue;
          }

          &.hotel {
            transition: background-color .3s;
            background-color: $hotel_blue;
          }

          &.retail {
            transition: background-color .3s;
            background-color: $teal;
          }
        }

        h1 {
          color: $white;
        }
      }

      .map__filters {
        background-color: transparent;
      }
    }

  }


  // hide google maps zooming buttons
  .gmnoprint.gm-bundled-control.gm-bundled-control-on-bottom {
    display: none;
  }
  .legend {
    margin: 15px;
    padding: 15px 30px;

    &__legend {
      flex-direction: column;
      padding-top: 15px;

      span {
        width: auto;
        align-self: flex-start;
      }

      &-items {
        width: 100%;

        .row {
          display: block;
        }

        .each {
          width: 50%;
          display: inline-block;
          float: left;
          position: relative;

          img,
          .circle {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
          }

          p {
            display: inline-block;
            padding-left: 35px;
          }
        }
      }
    }

    &__suggestedroutes {
      flex-direction: column;

      span {
        margin-right: 0;
        align-self: flex-start;
      }

      ul {
        width: 100%;
        display: block;

        li {
          margin-right: 0;
          float: left;
          width: 50%;
        }
      }
    }
  }
}

@include mobile {
  .map {
    &__info-scroll {
      width: 92%;
    }
    &__suggestedroutes {
      ul {
        li {
          margin-right: .5em;
        }
      }
    }
  }
  .legend {
    padding: 15px;

    &__suggestedroutes {
      .bar {
        width: 45px;
      }
    }
  }
}

@media (max-width: 350px) {
  .map__suggestedroutes {
    right: 5px;
    left: 5px;
    padding: 15px;
  }
}
