// sitemap styles
.sitemap {
  margin: 0;
  padding-bottom: 35px;
  margin-top: 100px;


  &__list-lvl2 {
    margin-left: 20px;

    list-style-type: lower-roman;
  }

  &__list-lvl3 {
    list-style-type: circle;
  }

  &__link {
    line-height: 1.9;

    color: $white;
  }
}
