// site-header styles
.header {
	display: block;
	width: 100%;
	z-index: 16;
	position: fixed;
	top: 0;

	background-image: url('/assets/ilight/images/ilight-bg.jpg');
	background-repeat: no-repeat;
	background-size: cover;

	&__additional {
		display: none;
	}

	&__container {
		border-bottom: 1px solid #626468;
		width: 100%	;

		.container {
			width: auto;
		}
	}

	.non-search {
		display: flex;

		&.hide {
			opacity: 0;
			visibility: hidden;
			overflow: hidden;
		}
	}

	.header-right {
		// float: right;

		width: 50%;

		text-align: right;
	}

	&__left {
		float: left;
	}

	&__right {
		float: right;
		margin-top: 10px;
		display: flex;
	}

	&__search {
		float: right;
		padding: 15px 0;
		z-index: 1;

		&.active {
			.icon-search {
				&:before {
					content: '\e903';
				}
			}
		}

		.mobile-search-icon {
			display: block;
			width: 100%;
			padding: 0;

			text-align: center;
			text-decoration: none;

			border: none;
			background: none;

			&:hover {
				.icon {
					transition: color .3s;
					color: $yellow;
				}
			}
		}

		.icon {
			font-size: 1.6em;
			display: inline-block;
			color: $white;
			transition: color .3s;
		}
	}

}
// site-logo styles
.site-logo {
	position: relative;
	z-index: 5;

	display: inline-block;

	padding: 10px 0;

	&__image {
		width: 160px;
		height: 50px;
		vertical-align: initial;
	}

	&__link {
		font-size: calc-em(28px);
		font-weight: $bold;
		line-height: 1em;

		display: inline-block;

		height: 100%;
		padding: 0 15px;

		vertical-align: middle;
		text-decoration: none;

		@include font-family('book');

		&:hover {
			text-decoration: none;
		}
	}
}

@include mobile-tablet {
	.header {
		position: sticky;
		top: 0;

		min-width: 300px;
		height: 120px;
		border-bottom: none;

		@include box-shadow(0, 2px, 20px, 0, rgba(0,0,0,.1));

		.container {
			@include gradient();
		}

		&__additional {
			display: flex;
			align-items: center;
			justify-content: space-between;
			flex-direction: row;
			padding: 10px 0;
			background-color: $black;

			.at-icon,
			.at-icon-wrapper {
				width: 23px !important;
				height: 23px !important;
			}

			.at-icon-wrapper {
				padding-left: 0;
			}

			.at-share-btn {
				background-color: transparent !important;
				margin: 0 !important;
				padding: 0 !important;
			}

			span {
				padding-left: 15px;
				font-size: calc-em(16px);
				@include font-family('demi');
			}

			ul {
				display: inline-block;
				padding-right: 20px;

				li {
					float: left;
					padding-left: 13px;
					display: flex;

					a {
						color: $yellow;
						outline: none;
					}
				}
			}
		}

		&__search {
			position: absolute;
			right: 70px;
			top: 15px;
		}
	}

	.site-logo {

		&__link {
			font-size: calc-em(20px);

			height: 60px;
			display: flex;
			align-items: center;
			padding-left: 0;
		}

		&__image {
			width: 130px;
		}

	}
}

.menu-notification {
	position: relative;
	z-index: 15;

	&.is_stuck {
		background: $green;
	}
}
