// sidebar styles
.sidebar {
  position: absolute;
  top: 0;
  margin-top: 50px;
  left: 15px;
  z-index: 16;


  .at-share-btn {
    margin: 0;
    background-color: transparent !important;
  }

  svg {
    width: 28px !important;
    height: 28px !important;
  }

  &__wrap {
    display: flex;
    width: auto;
    flex-direction: column;

    position: fixed;
    top: 150px;

    &.stick {
      position: absolute;
      top: -515px;
      bottom: auto;
    }
  }

  .share-wrap {
    display: flex;
    width: auto;
    padding: 0;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;

    position: relative;
    height: 100%;
  }

  &__dates {
    position: relative;
    left: 4px;

    display: block;
    writing-mode: vertical-rl;
    transform: rotate(-180deg);
    line-height: 0;
    user-select: none;

    @include font-family('demi');
    font-size: 1.0277777778em;

    // p {
    //   @include font-family('demi');
    //   font-size: 1.0277777778em;
    // }
  }

  &__social {
    display: block;
    margin: 35px 0;
    padding: 0;
    align-items: center;
    justify-content: center;

    a {
      display: block;
      color: $yellow;
      font-size: 1.3em;
      margin-bottom: 0px;
      filter: brightness(1);
      transition: filter .3s;

      &:last-of-type {
        margin-bottom: 0;
      }

      &:hover {
        transition: filter .3s;
        filter: brightness(1.3);
      }
    }
  }

  &__hashtag {
    position: relative;
    display: block;
    writing-mode: vertical-rl;
    transform: rotate(-180deg);
    line-height: 0;
    user-select: none;

    span {
      display: inline-block;
      @include font-family('light');
      font-size: 1.0277777778em;
    }

  }
}

@include mobile-tablet {
  .sidebar {
    display: none;
  }
}
