// breadcrumb styles
.breadcrumb {
  font-size: calc-em(14px);
  font-weight: $bold;

  position: relative;
  z-index: 2;

  text-transform: uppercase;

  padding: 0;
  margin-bottom: 0;
  background-color: transparent !important;

  ol {
    list-style-type: none;
    padding-left: 15px;
  }

  a {
    vertical-align: middle;
    text-decoration: none;

    color: $body-text;

    &:hover {
      span {
        color: $yellow;
      }
    }

    span {
      color: $white;
      @include font-family('light');
      font-size: calc-em(13px);
      letter-spacing: .05em;

      &.active {
        color: $yellow;
      }
    }
  }

  li {
    position: relative;

    display: inline-block;

    padding-right: 25px;

    vertical-align: middle;


    &::after {
      font-size: 1.6em;

      right: 0;

      display: inline-block;

      content: '\e905';
      vertical-align: middle;
      // position: relative;

      @include vertical-align();
      @extend %icomoon;
    }

    &:last-of-type {
      &::after {
        display: none;
      }
    }
  }
}
