// button styles

.button {
  position: relative;
  display: inline-block;

  img {
    position: absolute;
    top: -11px;
    right: 15px;
    z-index: 1;
  }

  &.cold {
    .button__link {
      /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#ad49c5+1,099d99+100 */
      background: rgb(173,73,197); /* Old browsers */
      background: -moz-linear-gradient(45deg, rgba(173,73,197,1) 1%, rgba(9,157,153,1) 100%); /* FF3.6-15 */
      background: -webkit-linear-gradient(45deg, rgba(173,73,197,1) 1%,rgba(9,157,153,1) 100%); /* Chrome10-25,Safari5.1-6 */
      background: linear-gradient(45deg, rgba(173,73,197,1) 1%,rgba(9,157,153,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
      filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ad49c5', endColorstr='#099d99',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
    }

    &:hover {
      .button__link {
        background: rgba(173,73,197,1);
      }
      .glow {
        --size: 80px;
      }
    }
  }


  &__link {
    position: relative;
    appearance: none;
    /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#1fd8df+1,eea70f+100 */
    background: rgb(31,216,223); /* Old browsers */
    background: -moz-linear-gradient(45deg, rgba(31,216,223,1) 1%, rgba(238,167,15,1) 100%); /* FF3.6-15 */
    background: -webkit-linear-gradient(45deg, rgba(31,216,223,1) 1%,rgba(238,167,15,1) 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(45deg, rgba(31,216,223,1) 1%,rgba(238,167,15,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#1fd8df', endColorstr='#eea70f',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */

    padding: 2px;
    border: none;
    color: white;
    font-size: 1.2em;
    cursor: pointer;
    outline: none;
    overflow: hidden;
    border-radius: 100px;
    width: 200px;

    display: inline-block;
  }

  &__text {
    color: $white;
    @include font-family('demi');
    font-size: calc-em(16px);

    padding: 2px;
    border-radius: 18px;

    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    z-index: 0;
    text-align: center;
  }

  .glow {
    --size: 0;
    position: absolute;
    left: 0;
    top: 0;
    width: var(--size);
    height: var(--size);
    background: radial-gradient(circle closest-side, rgba(31,216,223,1), transparent);
    transform: translate(-50%, -50%);
    transition: width .2s ease, height .2s ease;
  }

  &:hover {
    .button__link {
      background: rgba(238,167,15,1);
    }
    .glow {
      --size: 80px;
    }

  }
}
