// programmes-section styles
.programmes-section {
  padding: 70px 0;

  &__inner {
    justify-content: flex-end;
  }

  .col-3-md {
    position: relative;
  }

  &__header {
    padding-left: 30px;
    position: absolute;
    top: -60px;

    .line {
      @include line(150px);
      margin-left: 45px;
    }

    h1 {
      @include font-family('demi');
      font-size: calc-em(70px);
      margin: 0;
      letter-spacing: .2em;

      span {
        display: block;
        position: relative;

        &:last-of-type {
          left: 45px;
        }
      }
    }

    a {
      display: flex;
      color: $yellow;
      align-items: center;
      padding-top: .7em;
      margin-left: 45px;

      @include font-family('demi');
      font-size: calc-em(20px);

      &:hover {
        .icon {
          transform: translateX(12px);
          transition: transform .3s;
        }
      }

      .icon {
        font-size: 1.3em;
        transform: translateX(5px);
        transition: transform .3s;
        position: relative;
        top: -2px;
      }
    }
  }

  .programme {
    display: flex;
    align-items: center;
    background-color: transparent;
    border-top: 1px solid $pale-grey;
    padding: 1.5em;

    &:nth-child(odd) {
      background-color: $pale-purple;
    }

    &:last-child {
      border-bottom: 1px solid $pale-grey;
    }

    .col-2-md {
      text-align: center;
    }

    p {
      margin: 0;
    }

    .name {
      @include font-family('demi');
      font-size: calc-em(22px);
      max-width: 70%;
    }

    .dates {
      @include font-family('book');
      font-size: calc-em(20px);
    }

    .location {
      @include font-family('book');
      font-size: calc-em(20px);
    }

    .link {
      @include font-family('demi');
      font-size: calc-em(16px);

      border: 1px solid $yellow;
      background-color: transparent;

      border-radius: 18px;
      padding: 5px 30px;
      filter: brightness(1);
      transition: filter .3s;

      &:hover {
        transition: filter .3s;
        filter: brightness(1.3);
      }
    }
  }
}

@include mobile-tablet {
  .programmes-section {
    padding-top: 0;

    .col-12-md.handheld-only { // header in mobile
      display: flex;
      justify-content: flex-end;
      margin-bottom: 2em;
    }

    &__header {
      position: static;
      padding-left: 0;
      display: inline-block;
      width: 80%;

      h1 {

      }

      a {
        margin-left: 0;
      }

      .line {
        width: 100%;
        margin-left: 0;
      }
    }

    .programme {
      .name {
        max-width: 100%;

      }
      .col-2-md {
        text-align: left;
      }

      .link {
        display: inline-block;
        margin-top: 1em;
      }

      .location {
        margin-top: 1em;
      }
    }
  }
}

@include mobile {
  .programmes-section {
    &__header {
      h1 {
        font-size: calc-em(50px);

        span {
          &:last-of-type {
            left: 20px;
          }
        }
      }

      a {
        font-size: calc-em(20px);
      }
    }

    .programme {
      .dates {
        font-size: calc-em(20px);
      }
    }
  }
}
