// rte styles
.ui-state-default {
  background: $pale-purple;
  border: none;
  border-bottom: 1px solid #c5c5c5;
  outline: none;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
  position: relative;

  &:before {
    content: '\e907';
    font-family: 'icomoon';
    position: absolute;
    right: 15px;
    top: 50%;
    transform: translateY(-50%);
    font-size: 1.5em;
  }

  &.ui-state-active {
    &:before {
      content: '\e904';
    }
  }
}

.ui-accordion {
  .ui-accordion-content {
    overflow: hidden;
    border: none;
    color: white;
    background: transparent;
  }
}

.rte {
  position: relative;

  margin-bottom: 20px;

  .accordion {

  }

   > * {
    &:first-child {
      margin-top: 0;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

  a {
    color: $white;

    &:hover {
      text-decoration: underline;
    }
  }

  img {
    margin-bottom: 10px;

    box-shadow: 0 0 10px rgba(0, 0, 0, .1);
  }

  address {
    font-style: normal;
  }
  .table {
    overflow-x: auto;
  }
  table {
    th {
      padding: 5px 10px;

      text-align: center;
    }
    td {
      padding: 5px 10px;
    }
  }

  ul {
    margin-left: 20px;

    list-style-type: disc;

    ul {
      list-style: circle;
    }
  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  p,
  ul,
  ol {
    &:first-child {
      margin-top: 0;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

  h1,
  h2,
  h3 {
    color: $white;
  }

  figure {
    margin: 10px 0;
  }

  figcaption {
    font-style: italic;
  }

  blockquote {
    font-size: calc-em(24px);
    font-style: italic;
    line-height: calc-em(22px);

    position: relative;
    z-index: 1;

    margin: 0;
    // line-height: 1.4;
    padding: 50px 20px 20px 45px;



    &::before {
      font-size: calc-em(80px);
      font-weight: bold;
      font-style: normal;
      line-height: normal;

      position: absolute;
      z-index: -1;
      top: 0;
      left: 0;

      content: '\201C'; /*Unicode for Left Double Quote*/

      color: #e8e8e8;
    }

    &::after {
      /*Reset to make sure*/
      content: '';
    }
  }
}

