// video-section styles
.video-section {
   margin-bottom: 145px;
   padding-top: 70px; // to preserve background flow

  .row-col-12 {
    justify-content: flex-end;
  }

  &__container {
    position: relative;
    outline: none;
  }

  &__inner {
    height: 450px;
    overflow: hidden;
    position: relative;
  }

  &__overlay {
    position: absolute;
    right: 0;
    left: 0;
    top: 0;
    bottom: 0;
    background-color: rgba(0,0,0,.5);
  }

  &__background {
    position: absolute;
    right: 0;
    left: 0;

    video {
      width: 100%;
      height: 100%;
    }
  }

  &__content {
    position: absolute;
    bottom: -75px;
    left: 75px;

    p {
      @include font-family('light');
      font-size: calc-em(26px);
      margin: 0;
      padding-bottom: .5em;
    }

    h1 {
      @include font-family('demi');
      font-size: calc-em(60px);
      margin: 0;

      span {
        display: block;
        letter-spacing: .2em;
        line-height: 1;
        padding-bottom: .15em;
      }
    }

    .hashtag {
      @include font-family('light');
      font-size: calc-em(26px);
    }
  }
}

@include mobile-tablet {
  .video-section {
    padding-top: 0;
    
    &__content {
      position: absolute;
      bottom: 0;
      left: 0;
      padding: 0 30px;
    }
  }
}

@include mobile {
  .video-section {

    &__content {
      bottom: -100px;

      p,
      .hashtag {
        font-size: calc-em(22px);
      }

      h1 {
        font-size: calc-em(50px);

        span {
          line-height: 1.15;
          padding-bottom: 0;
        }
      }
    }

    &__background {
      video {
        width: auto;
        height: 400px;
      }
    }

    &__inner {
      height: 400px;
    }
  }
}
