@font-face {
	font-family: 'icomoon';
	font-weight: normal;
	font-style: normal;

	src: url('/assets/ilight/fonts/icomoon.eot?qggsew');
	src: url('/assets/ilight/fonts/icomoon.eot?qggsew#iefix') format('embedded-opentype'),
		url('/assets/ilight/fonts/icomoon.ttf?qggsew') format('truetype'),
		url('/assets/ilight/fonts/icomoon.woff?qggsew') format('woff'),
		url('/assets/ilight/fonts/icomoon.svg?qggsew#icomoon') format('svg');
}

[class^='icon-'],
[class*=' icon-'] {
	/* use !important to prevent issues with browser extensions that change fonts */
	font-family: 'icomoon' !important;
	font-weight: normal;
	font-style: normal;
	font-variant: normal;
	line-height: 1;

	vertical-align: initial;
	text-transform: none;

	speak: none;
	/* Better Font Rendering =========== */
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;

	&:before {
		font-family: 'icomoon' !important;
		font-weight: normal;
		font-style: normal;
		font-variant: normal;
		line-height: 1;

		vertical-align: initial;
		text-transform: none;

		speak: none;
		/* Better Font Rendering =========== */
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
	}
}

%icomoon {
	/* use !important to prevent issues with browser extensions that change fonts */
	font-family: 'icomoon' !important;
	font-weight: normal;
	font-style: normal;
	font-variant: normal;
	line-height: 1;

	vertical-align: initial;
	text-transform: none;

	speak: none;
	/* Better Font Rendering =========== */
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}


