// pagination styles

.pagination {
  display: block;
  list-style: none;

  @include clearfix();

  padding-bottom: 70px;


  li {
    display: inline-block;
    margin-right: 5px;
    border-collapse: collapse;



    a {
      line-height: 38px;

      display: block;
      width: 25px;
      height: 40px;

      text-align: center;
      text-decoration: none;
      color: $white;

      span {
        font-size: 1.4em;
      }

      .number {
        font-size: 1em;
      }

      &:hover {
        text-decoration: underline;
      }
    }
  }

  .active {
    a {
      color: $yellow;
      font-weight: bolder;
    }
  }
}

@include mobile-tablet {
  .pagination {
    text-align: center;
  }
}
