// search-results styles
.search-page {
  // padding-top: 74px;
  min-height: 700px;
}

.search-results {

  &__results {
    padding-bottom: 70px;
  }

  &__each {
    display: block;
    margin: 2em 0;
    word-break: break-word;

    a {
      display: block;
      text-decoration: underline;
    }

    .url {
      font-size: .875em;
      color: $yellow;
    }

    .teaser {
      margin: 0;
      font-size: .875em;
    }
  }
}



@include mobile-tablet {
  .search-results {
    padding: 0 15px;
  }
}
