
/*==============================
>> Desktop Search Box
================================ */

.d-search-bar {
  font-size: calc-em(13px);

  position: absolute;
  right: 55px;
  top: 18px;
  display: block;
  float: right;

  width: 100%;
  max-width: 480px;
  z-index: 2;
  transform: scaleX(0);
  transform-origin: right;
  transition: transform .3s;

  z-index: 10;

  &.expand {
    transition: transform .3s;
    transform: scaleX(1);
  }

  .siteSearchDropdown {
    float: left;

    width: 50%;
    height: 25px;
    padding: 0 25px 0 10px;

    border: none;
    border-radius: 0;
    background-color: $grey;
  }

  .siteSearch {
    float: right;

    width: 100%;
    height: 35px;
    padding: 0 36px 0 15px;

    border: 0;
    border: 1px solid $grey;
    color: $white;
    background-color: transparent;
    border: 1px solid transparent;
    border-bottom: 1px solid $white;
    font-size: calc-em(26px);

    &::-webkit-input-placeholder {
      /* Chrome/Opera/Safari */
      color: $white;
    }
    &:focus::-webkit-input-placeholder {
      /* Chrome/Opera/Safari */
      color: $white;
    }

    &::-moz-placeholder {
      /* Firefox 19+ */
      color: $white;
    }
    &:focus::-moz-placeholder {
      /* Firefox 19+ */
      color: $white;
    }

    &:-ms-input-placeholder {
      /* IE 10+ */
      color: $white;
    }

    &:focus:-ms-input-placeholder {
      /* IE 10+ */
      color: $white;
    }

    &:-moz-placeholder {
      /* Firefox 18- */
      color: $white;
    }

    &:focus:-moz-placeholder {
      /* Firefox 18- */
      color: $white;
    }
  }

  .btn-search {
    font-size: 1.3em;

    position: absolute;
    z-index: 1;
    top: 6px;
    right: 0;

    width: 40px;
    height: 25px;

    text-align: center;

    color: $white;
    border: 0;
    background: none;

    .icon-search {
      font-size: 1.7em;
    }
  }
}

@include mobile-tablet {
  .d-search-bar {
    display: none;
  }
}


/*==============================
>> Mobile Search Box
================================ */

.m-search-bar {
  position: absolute;
  z-index: 99;
  top: auto;
  left: 0;

  width: 100%;
  padding: 15px;

  transform: scaleY(0);
  transform-origin: top center;

  color: $black;
  background: $white-light;
  @include transition(transform, 150ms, ease-in-out);

  .mobileSearch {
    position: relative;

    width: 100%;
    padding: 5px 25px 5px 5px;

    border: 0;
    border-bottom: 1px solid $green;
    outline: 0;
    background: none;
    // font-size: 0.875em;
    // @include transition(border, .15s, ease-in-out);

    &::-webkit-input-placeholder,
    &:-moz-placeholder,
    &::-moz-placeholder,
    &:-ms-input-placeholder {
      color: $body-text;
    }
  }

  &.visible {
    transform: scaleY(1);
  }

  .btn-search {
    font-size: 1.5em;

    position: absolute;
    top: auto;
    right: 15px;
    bottom: 15px;

    padding: 0;

    border: 0;
    background: none;


    .icon {
      display: inline-block;

      vertical-align: middle;
    }
  }
}
