// link-btn styles
.link-btn {
  display: inline-block;

  padding: 3px 30px;

  text-decoration: none;
  text-transform: uppercase;

  color: $white;
  border: 1px solid $white;
  border-radius: 20px;

  &:hover {
    text-decoration: none;

    color: $body-text;
    border: 1px solid $body-text;
    background: $white;
  }

  &--solid {
    border: 1px solid $green;
    background: $green;
    // &:hover {
    //   color: $white;
    //   border: 1px solid $black-medium;
    //   background: $black-medium;
    // }
  }
}

@include mobile {
  .link-btn {
    margin: 15px 0;

    &--no-margin {
      margin: 0;
    }
    &--mbl-solid {
      border: 1px solid $green;
      background: $green;
      // &:hover {
      //   color: $white;
      //   border: 1px solid $black-medium;
      //   background: $black-medium;
      // }
    }
  }
}
