// page-header styles

.page-header {
	position: relative;

	display: flex;
	flex-direction: column;

	width: 100%;
	min-height: 120px;
	margin: 0 auto;
	padding: 35px 0;

	color: $body-text;
	background-color: $green;

	align-items: center;
	justify-content: center;
	@include background-cover(center, top);

	.overlay {
		position: absolute;
		top: 0;

		width: 100%;
		height: 100%;

		@include background-gradient(rgba(0,0,0,.3), rgba(0, 0, 0, 0), vertical);
	}

	&__title {
		font-size: calc-em(42px);
		font-weight: $medium;

		position: relative;
		z-index: 2;

		margin: 0;

		text-align: center;
		text-transform: uppercase;

		color: $white;

		
	}

	&__dropDown {
		font-size: calc-em(20px);
		font-weight: $bold;

		z-index: 2;

		text-align: center;

		color: $green;
		background: $grey;

		
	}

	&__subTitle {
		font-size: calc-em(20px);

		display: block;

		margin: 5px 0 0;
		padding: 0 20px;

		text-align: center;
		text-transform: none;

		color: $green;
		background: $grey;

		
	}
}

@include desktop {
	.page-header {
		min-height: 235px;

		&__title {
			font-size: calc-em(54px);
		}
	}
}
