// acknowledgements styles
.acknowledgements-page {
  padding-bottom: 40px;
}

.acknowledgements {

  &-section {
    padding-left: 70px;
    padding-bottom: 40px;
  }

  &__title {
    font-size: calc-em(50px);
    letter-spacing: .2em;
    margin: 0;
    @include font-family('demi');

    h1 {
      margin: 0;
    }
  }

  &__header {
    border-bottom: 1px solid rgba(255,255,255,.3);
    margin-bottom: 36px;
  }

  &__row {
    padding: 20px 70px 40px 10px;

    h2 {
      margin-top: 0;
      margin-bottom: 12px;
    }

    img {
      height: 40px;
    }
  }
}

@include mobile-tablet {
  .acknowledgements {

    &__title {
      padding-top: 30px;
      padding-left: 30px;

      font-size: 2em;
    }

    &-section {
      padding-left: 0;
    }

    &__each {
      padding-bottom: 25px;
    }

  }
}

@include mobile {
  .acknowledgements {

    &__title {
      display: none;
    }

  }
}
