// topnav-menu styles
.topnav-menu {
  margin-bottom: 5px;
  ul {
    @extend %plainlist;
  }
  li {
    position: relative;

    display: inline-block;

    padding: 0 10px;

    &::after {
      position: absolute;
      top: 5px;
      right: 0;

      display: block;

      width: 1px;
      height: 15px;

      content: '';

      background: $grey;
    }

    &:last-child {
      padding-right: 0;

      &::after {
        display: none;
      }
    }

    a {
      font-size: calc-em(14px);

      text-decoration: none;

      color: $black;

      &:hover {
        text-decoration: underline;
      }
    }
  }
}
